export const GET_TERIMA_SUPPLIER_TEMP = "GET_TERIMA_SUPPLIER_TEMP"
export const GET_RETURN_SUPPLIER_TEMP = "GET_RETURN_SUPPLIER_TEMP"
export const SET_TOTAL_NW_TERIMA_SUPPLIER = "SET_TOTAL_NW_TERIMA_SUPPLIER"
export const SET_TOTAL_BRUTO_TERIMA_SUPPLIER = "SET_TOTAL_BRUTO_TERIMA_SUPPLIER"
export const SET_TOTAL_PKG_TERIMA_SUPPLIER = "SET_TOTAL_PKG_TERIMA_SUPPLIER"
export const SET_TOTAL_GROSS_TERIMA_SUPPLIER = "SET_TOTAL_GROSS_TERIMA_SUPPLIER"
export const SET_TOTAL_ONGKIR_TERIMA_SUPPLIER = "SET_TOTAL_ONGKIR_TERIMA_SUPPLIER"

export const getTerimaSupplierTemp = () => {
    return (dispatch) => {
        dispatch({
            type: GET_TERIMA_SUPPLIER_TEMP,
            payload: {
                data: JSON.parse(localStorage.getItem("TerimaSupplierTable")) === null ? [] : JSON.parse(localStorage.getItem("TerimaSupplierTable")),
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                error: false
            }
        })
    }
}
export const setTotalNW = (total) => {
    return (dispatch) => {
        dispatch({
            type: SET_TOTAL_NW_TERIMA_SUPPLIER,
            payload: {
                data: localStorage.getItem("TotalNW") || 0,
                alert: "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
                error: false
            }
        })
    }
}
