export const GET_DATA_USER = "GET_DATA_USER";

export const getUserData = (idUser, namaUser, level, password) => {
  return (dispatch) => {
    dispatch({
      type: GET_DATA_USER,
      payload: {
        idUser: idUser,
        namaUser: namaUser,
        level: level,
        password: password,
      },
    });
  };
};
